.name {
	justify-self: end;
	font-size: 2rem;
}

.aboutContainer {
  margin-top: 100px;
  min-height: 100vh;
  max-width: 1536px;
  margin-left: auto;
  margin-right: auto;
  padding-left: 170px;
  padding-right: 170px; 
 

}

.aboutContentGrid {
	display: grid;
	grid-template-columns: repeat(auto-fit, minmax(300px,1fr));
	gap: 100px;
	align-items: center;
}

.image {
	justify-self: end;
	width: 200px;
	height: 200px;
	border-radius: 50%;
	object-fit: cover;
} 

.description {
	margin-top: 100px;
	font-size: 2rem;
	line-height: 1.5;
}

.aboutLinks {
	margin-top: 50px;
	margin-bottom: 100px;
	display: flex;
	 
	align-items: center;
	gap: 10px;
	font-size: 2rem;

}

.link {
	color: black;
	transition: color 0.3s ease;
	text-decoration: underline;
	cursor: pointer;
}
.seeProjectsButton {
	margin-top: 100px;
	padding: 15px 30px;
	font-size: 1.5rem;
	background-color: black;
	color: white;
	border: none;
	border-radius: 5px;
	cursor: pointer;
	transition: background-color 0.3s ease;
}


@media screen and (min-width: 769px) {
    .name {
		font-size: 10rem;
	}
	.image {
		width: 300px;
		height: 300px;
	}

    
	
}

@media screen and (min-width: 1260px) {
	.image {
		margin-left:100px;
	}
	
}